/* Styles for the main container */
.image-reveal-container {
  position: relative;
  width: calc(100vw - 2em); /* Adjusted to give space on the sides */
  height: calc(
    100vh - 3em
  ); /* Adjusted to give space for the title and instructions */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

/* Styles for the image */
.image-reveal-image {
  max-width: 100vw;
  max-height: 90vh;
}

/* Styles for the instruction div */
.image-reveal-instruction {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  border-radius: 5px;
  z-index: 10;
}

/* Styles for the overlay boxes */
.image-box {
  position: absolute;
  background-color: rgba(173, 216, 230, 1);
  border: 1px solid gray;
  cursor: pointer;
  display: block; /* Ensures the box is visible to start */
}

.image-box-reveal {
  cursor: none !important;
  animation-name: dissolve; /* Refer to the keyframes rule */
  animation-duration: 1.5s; /* Animation lasts for 1.5 seconds */
  animation-fill-mode: forwards; /* Keeps the box at the final state of the animation */
}

@keyframes dissolve {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
